.users {
	box-sizing: border-box;
	padding: 10px;
	height: 100%;
	display: flex;
	justify-content: flex-start;
	flex-direction: column;
	gap: 10px;
	background: center no-repeat url("../../assets/bgU.jpg");
	background-size: cover;
}

.block {
	box-sizing: border-box;
	display: flex;
	gap: 25px;
	width: 100%;
	justify-content: space-between;
	align-items: center;
	font-size: larger;
	font-weight: bolder;
	text-decoration: none;
	color: #b1e5e7;
	border: 2px solid #b1e5e7;
	border-radius: 10px;
	padding: 5px;
}

.blockImg {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	gap: 5px;
	padding: 5px;
	height: 100%;
	min-width: 100px;

	& img {
		border-radius: 50%;
	}
}

.blockMain {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	flex: 1;
	gap: 5px;
	padding: 2px;
	height: 100%;
	& a {
		color: #b1e5e7;
		text-decoration: none;
	}
}

.status {
	font-size: 14px;
	font-weight: unset;
}

.blockLocation {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	gap: 5px;
	padding: 5px;
	height: 100%;
	width: 10%;
	min-width: 200px;
}
